export default {
	data() {
		return {
			staticURL: this.$url.staticURL,
			uploadURL: this.$url.uploadURL,
			genderIndex: 0,
			gender: ['男', '女'],
			educationIndex: 2,
			education: ['初中','高中','大专','大学本科','研究生'],
			moneyIndex: 2,
			money: ['1000-3000','3000-5000','5000-7000','7000以上'],
			jobIndex: 2,
			job: ['服务员','收银员','厨师','经理'],
			name: 'carson',
			age: '29',
			docPath:'',
			docName:''
		};
	},
	methods: {
		submit() {
			const vm = this;
			if (vm.tel.length != 11) {
				vm.$utils.toast('请将手机号输入完整');
				return false;
			} else if (!vm.docPath) {
				vm.$utils.toast('请上传简历');
				return false;
			}
			if (!vm.name || !vm.card) {
				vm.$utils.toast('请将信息填写完整');
				return false;
			}
			uni.showModal({
				content: '确认提交',
				success: function(res) {
					if (res.confirm) {
						vm.$utils.toast('简历提交成功,预计将在24h内予以审核');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		// 上传文件
		addDoc() {
			const vm = this;
			uni.chooseMessageFile({
			  count: 1,
			  type: 'file',
			  extension: ['doc'],
			  success (res) {
			    console.log(res);
				vm.docPath = res.tempFiles[0].path
				vm.docName = res.tempFiles[0].name
			  }
			})
			wx.uploadFile({
			  url: 'https://example.weixin.qq.com/upload',
			  filePath: vm.docPath,
			  name: 'file',
			  formData: {
				'user': 'test'
			  },
			  success (res){
			  }
			})
		},
		changeGender(e) {
			this.genderIndex = e.target.value
		},
		changeEducation(e) {
			this.educationIndex = e.target.value
		},
		changeMoney(e) {
			this.moneyIndex = e.target.value
		},
		changeJob(e) {
			this.jobIndex = e.target.value
		},
		goPage() {
			this.$utils.goPage(`/pages/mainAll/addressAdd/addressAdd`)
		}
	}
}
